export function jump(url, type, params) {

	switch (type) {
		case 'params': //
			uni.navigateTo({
				url: url + params
			});
			break;
		case 'navigate': //
			uni.navigateTo({
				url: url
			});
			break;
		case 'redirect': //, 
			uni.redirectTo({
				url: url
			});
			break;
		case 'reLaunch': //, 
			uni.reLaunch({
				url: url
			});
			break;
		case 'switch': //tabbar
			uni.switchTab({
				url: url
			});
			break;
		case 'web':
			uni.navigateTo({ //
				url: '/pages/web/index?link=' + encodeURIComponent(JSON.stringify(url))
			});
			break;
		default: //
			uni.navigateTo({
				url: url
			});
			break;
			// 
			// uni.navigateTo({
			// 	url: '/pages/index/index?myData=' + encodeURIComponent(JSON.stringify(row))
			// })
	}
}